.searhBar {
  background-color: lightblue;
}

.title {
  justify-content: center;
}

h2 {
  align-self: center;
  padding-left: 2rem;
}

.gallery {
  border: 1px solid #ccc;
  height: 60%;  
  background-color: #d9ffb3;
  font-family: 'Open Sans', sans-serif;; 

 
}

.gallery:hover {
  border: 2px solid #777;
}

.gallery img {
  width: 100%;
  height: auto;
  
}

.desc {
  padding: 15px;
  text-align: center;  
  
}

* {
  box-sizing: border-box;
}

.box {
  display: flex-wrap;
  justify-content: space-between;
}

.responsive {
  padding: 1rem;
  float: left;
  width: 24.99999%;  
}

.heart {

}

@media only screen and (max-width: 700px) {
  .responsive {
    width: 49.99999%;
    margin: 6px 0;
   
    
  }
}

@media only screen and (max-width: 500px) {
  .responsive {
    width: 100%;   

  }
}




